import { Form, Button, Container, Row, Col } from 'react-bootstrap';
import { useState } from 'react';
import OwnCardsSelector from './OwnCardsSelector';
import {loadInstrumentCards} from '../utils/RisingStarUtils';

/*
Poner cards para trade:
Dejar disponibles tus cards para que pueden ser intercambiadas seleccionando por cuales cards se pueden intercambiar.Puede ser 1=>1 o N=>1 o 1=>N
.- Obtener todas las CARDS (de instrumentos en principio) y paginar
.- Busqueda (fulltext)
.- Seleccion de a una o varias.
.- Identificar los stats de las seleccionadas (el total de las seleccionadas)
.- Busqueda sobre todas las CARDS para ver por cual intercambiar con 2 opciones:
    - Buscar por fulltext dentro de una leccion y seleccionar 1 o mas cartas
        * Ej.: Buscar por R105
    - Dejar abierto a cualquier cantidad de una misma leccion
        * Ej: Seleccionada 1 carta de 150luck de Gitarra por cualquier cantidad de cards que sumen 150luck de piano
.- Agregar cartas para el trade
.- Submitear ¿transaction? con el origen y el destino

Buscar cards para trade:
- Seleccionar 1 o mas cards y buscar trades disponibles por total de luck y por carta especifica
- Buscar posibles trades disponibles segun las cartas de una leccion

*/
function CardTrade() {
    const [ cargando, setCargando ] = useState(false);
    const [ data, setData ] = useState([]);
    const [ username, setUsername ] = useState("jotapdiez");

    const changeUsername = (e) => {
        setUsername(e.target.value);
    }

    const collect = async () => {
        setCargando(true);
        const allCards = await loadInstrumentCards(username.toLowerCase());
        setData(allCards);
        setCargando(false);
    }

    // const collectLogin = () => {
    //     setCargando(true);
    //     // TODO: Mantener el login
    //     if (window.hive_keychain) {
    //         window.hive_keychain.requestHandshake(() => {
    //             // isHiveKeychainAvailable(true);
    //             console.log('Keychain Found!');
    //         })
    //     } else {
    //         alert("Hive KeyChain needed to login");
    //         setCargando(false);
    //         return;
    //     }

    //     //Login
    //     window.hive_keychain.requestSignBuffer(username.toLowerCase(), 'RS Tools Login (DEV)', 'Posting', function (response, err) {
    //         if (response.success) {
    //             try {
    //                 const allCards = loadInstrumentCards(username.toLowerCase());
    //                 setData(allCards);
    //             } catch (e) {
    //                 console.error(e);
    //             }
    //         } else { // ELSE, SOMETHING FAILED UPON LOGIN, PRINT ERROR TO CONSOLE
    //             console.error(response.message);
    //             alert("Login error")
    //         }

    //         setCargando(false);
    //     });
    // }

    return (<>
        <Container style={{paddingBottom: '60px'}}>
            <Row>
                <Col>
                    <Form onSubmit={e => { e.preventDefault(); }}>
                    <Form.Group className="mb-3" controlId="fieldGroup">
                        <Form.Label>RisingStar Username</Form.Label>
                        <Form.Control type="username" placeholder="Enter your username" value={username} onChange={changeUsername}/>
                    </Form.Group>
                    <Form.Group className="mb-3" controlId="buttonGroup">
                        <Button variant="primary" type="submit" onClick={collect}>Calculate</Button>
                    </Form.Group>
                    </Form>
                </Col>
            </Row>
            <Row>
                <Col>
                    <div style={{ display: 'flex', flexWrap: 'wrap' }}>
                        {cargando ?
                            <div>Loading your cards...</div> :
                            (data.length === 0 ?
                                <span>Login to continue</span> :
                                <OwnCardsSelector cardsData={data} />
                            )
                        }
                        {/*
                            - Seleccionar cartas a cambiar (origen)
                            - Seleccionar carta por la cual cambiar (destino)
                        */}
                    </div>
                </Col>
            </Row>
        </Container>
    </>);
}

export default CardTrade;