import { useState, useEffect } from "react";

function NFTCard({card, cantidad, selectable, isSelected, onSelect}) {
    const [ext, setExt] = useState('.png');

    useEffect(() => {
        let number = card.properties.type.indexOf(' ') > 0 ? card.properties.type.substring(0, card.properties.type.indexOf(' ')).toLowerCase() : card.properties.type.toLowerCase();
        const extension = number.endsWith('a') ? ".gif" : ".png";
        setExt(extension);
    }, [card]);

    const _handleSelect = () => {
        if (!selectable) return;

        onSelect(card, !isSelected);
    }

    return (<>
        <div className={`rscard ${!selectable || isSelected ? 'rscardSelected' : 'rscardNoSelected'}`} data-toggle="modal" data-target="#nftDetails" onClick={_handleSelect}>
                <span className="badge bg-warning text-dark cant-cartas">{cantidad}</span>
                <div className="card-image">
                    <img src={"https://www.risingstargame.com/images/cards/"+card.properties.type+ext} alt={card._id} className="img-fluid gray-image card-img" />
                </div>
            </div>
        </>);
}

export default NFTCard;