import SSC from 'sscjs';

const fetchData = async (username, offset) => {
    let ssc_script = new SSC('https://custr.de.ryamer.com');
    if (ssc_script !== null) {
        let data = await ssc_script.find(
            "nft", //contract
            "STARinstances", //table
            {
                account: username
            }, //query
            1000, //limit
            offset, //offset
            [] //indexes
        );

        return data;
    } else {
        console.log('sin ssc');
        return [];
    }
}

const loadAllCards = async (username, offset, cards) => {
    // console.log('*** loadAllCards offset = ', offset);
    const allCards = await fetchData(username, offset).then((data) => {
        // console.log('*** ' + offset + ' - data = ', data);
        if (data !== null && data.length > 0) {
            //filtro solo cards de las lecciones
            const filteredCards = data.filter(e => e.properties.class === "instrument" || e.properties.class === "transport" || e.properties.class === "crafted");
            cards = [...cards, ...filteredCards];
            //pido el resto
            return loadAllCards(username, offset + data.length, cards);
        } else {
            // console.log('*** final cards = ', cards);
            return cards;
        }
    });
    // console.log('*** offset ' +offset+' | allCards = ', allCards);
    return allCards;
}

export const loadInstrumentCards = async (username) => {
    let cardsData = loadAllCards(username, 0, []);
    return cardsData;
}

export const parseStats = function (stats) {
    //"stats": "100,1,75,3"
    //stat=fans,luck,skill,IM
    const statsSplit = stats.split(',');
    return { fans: statsSplit[0], luck: statsSplit[1], skill: statsSplit[2], IM: statsSplit[3] };
}

