import NFTCard from './NFTCard';

function NFTCardList({cards, isSelectable, onSelect}) {
    const handleSelect = (card, selected) => {
        if (onSelect) onSelect(card, selected);
    }

    return cards.map((card, index) =>
        <NFTCard key={card.data._id} card={card.data} cantidad={card.cant} selectable={isSelectable} onSelect={handleSelect} />
    );
}

export default NFTCardList;